import React, { useState } from "react";
import * as styles from "../../styles/sitesDemo.module.css";

const Spreadsheet = () => {
    const [grid, setGrid] = useState([
        ["name", "Walkum"],
        ["phone", "867-5309"],
        ["map", "map url"],
        ["social media", "facebook.com"],
    ]);

    const handleCellChange = (row, col, value) => {
        if (col === 1) { // Allow changes only in the second column
            const newGrid = grid.map((rowArr, rowIndex) =>
                rowIndex === row ? [rowArr[0], value] : rowArr
            );
            setGrid(newGrid);
        }
    };

    return (
        <div className={styles.spreadsheet}>
            <table>
                <thead>
                    <tr>
                        <th>DEMO</th>
                        <th>EDIT BELOW</th>
                    </tr>
                </thead>
                <tbody>
                    {grid.map((row, rowIndex) => (
                        <tr key={rowIndex}>
                            <td className={styles.uneditableCell}>{row[0]}</td>
                            <td>
                                <input
                                    type="text"
                                    value={row[1]}
                                    onChange={(e) => handleCellChange(rowIndex, 1, e.target.value)}
                                />
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default Spreadsheet;
